@use  "./texts.scss";
aside {
  display: inline-block;
  //width: 400px;
  margin-top: 110px;
  //background: color(dark);
  position: relative;
  //position: fixed;
  //top: 12vh;
  //left: 0;
  .box  {
    width: 215px;
    height: 50px;
    position: relative;

    @media (max-width: 400px) {
      width: 100% ;

    }






    //height: 70px;
      a {
        z-index: 5;
        display: block;
        width: 200px;
        @include text-primary(18px , color(light));
        //background: rgba(78, 78, 78, 0);

          text-shadow: 1px 1px 5px #000000;
        position: fixed;

        font-weight: 400;
        letter-spacing: .2em ;
        padding-left: 20px;
        line-height: 46px;

        //background: color(dark);
        transition: .3s  ease-in-out;

        &:hover  {
          width: 255px;
          //text-align: right;
          //padding-left: 200px;
          background: rgba(78, 78, 78, 1);
          //padding-right: 20px;
          padding-left: 50px;
          box-shadow: 1px 0 1px #1a1717;


        }

      }
    }

}