* {
  padding: 0 ;
  margin: 0;
  box-sizing: border-box;
}
@import "./texts";
@import "variables";
@import "./maps";
@import "./maps";
@import "./sidebar";
@import "./wrapper";
@import "./media";
body {
  background: color(dark);
}


.container {
  max-width: 1100px;
  margin: 0 auto;
  //padding: 0 30px 0 30px;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
}
header {
  position: fixed;
  top: 0;
  z-index: 1;
  width: 100%;
  height: 12vh;
  background: color(dark);

  color: #ffffff;
   nav {
     display: flex;
     width: 100%;
     justify-content: center;

     box-shadow: 1px 1px 5px black ;
     background: color(dark);
     @media (max-width: 740px) {
       font-size: 20px;
       line-height: 80px;
       justify-content: center;

     }


     .logo  {
       //width: 300px;
       padding-left: 20px;
       height: 100%;
       @include text-primary(30px, white);
       line-height : 80px;
       @media (max-width: 740px) {
         font-size: 19px;
         line-height: 20px;
         //padding: 20px 10px;
         align-items: center;
         margin: 30px auto;
       }

     }

     .btn {
       margin-left: 50px;
       width: 100px;
       height: 30px;
       background: color(darker);
       color: #ffffff;
       border: none;
       border-radius: 5px;
       display: none;

     }
     ul#menu1 {
       display: flex;
       margin-left: auto;
       justify-content: space-around;
       margin-right: 20px;
       li {
         //margin-left: 20px;
         height: 100%;



         a  {
           display: block;
           @include text-primary(16px, white);
           font-weight: 400;
           line-height: 98px;
           padding: 0 10px;
           transition: .4s  ease-in-out;

           &:hover {
             background: rgba(78, 78, 78, 1);
             text-shadow:   1px 2px 10px #1a1717;


           }
           &.active {
             background: rgba(78, 78, 78, 1);
             text-shadow:   1px 2px 10px #1a1717;


           }
           @media (max-width: 740px) {
             display: none;
           }


         }
       }

     }


   }
}
span {
  color: color(special);
  font-family: Verdana, sans-serif;
  font-size: 1.2em;
  font-style: italic;
}


